<template>
		<div class="weui-cell__ft">
		<div class="marks-minus" 
		@click="minusScore()" v-if="this.commited=='no'"><span class="icon-remove_circle_outline"></span></div>
			<div class="marks-show">
				{{score}}
			</div>
		<div class="marks-add" 
		@click="addScore()" v-if="this.commited=='no'"><span class="icon-add_circle"></span></div>
	
	</div>

</template>
<script type="text/javascript">

	export default {
		props :['scoreLevel','commited'],
		data(){
			return {
				
			}
		},
		mounted(){ 

			 
		},
		computed:{
			score:function(){
				return this.$store.state.score.score[this.scoreLevel-1];
			}
		},
		methods: {

			addScore(){
					this.$store.commit('addScore',this.scoreLevel);
			},

			minusScore(){
					this.$store.commit('reduceScore',this.scoreLevel);
			}
		}
}
</script>

<style scoped>

	.marks-show {
		display: inline-block;
    	line-height: 22px;
    	text-align: center;
    	vertical-align: top;
    	padding: 6px 0;
    	color: #666;
    	width: 20px;
	}

	.marks-minus{
		position: absolute;
		right: 80px;
		display: inline-block;
		line-height: 24px;
		font-size: 24px;
    	color: #00a0dc;
    	padding: 6px;
    	text-align: center;
	}

	.marks-add {
		display: inline-block;
		line-height: 24px;
    	font-size: 24px;
    	color: #00a0dc;
    	padding: 6px;
    	text-align: center;
	}
	
</style>


